.BreadTop{
    width:80%;
    height:20%;
    margin:auto;
    background:linear-gradient(#bc581e, #e27b36);
    border-radius:50% 50% 0 0;
    position:relative;
  }
  
  .Seed{
    position:absolute;
    width:10%;
    height:15%;
    background-color:#fff;
    top:50%;
    left:30%;
    border-radius:20px;
    transform: rotate(-20deg);
    box-shadow:inset -2px -3px  #c9c9c9;
  }
  
  .Second{
    top:25%;
    left:50%;
      transform: rotate(20deg);
  }
  
  .Third{
    top:35%;
    left:78%;
      transform: rotate(-195deg);
  }
  
  .Fourth{
    top:45%;
    left:10%;
      transform: rotate(-170deg);
  }
